import React from "react";
import { graphql } from "gatsby";
import Layout from "../components/layout";

const linkStyle = {
  textDecoration: "none",
  color: "green"
};
const AuthorIs = () => (
  <a style={linkStyle} href="https://www.twitter.com/phatbhoy67">
    @phatbhoy67
  </a>
);
export default ({ data }) => {
  const post = data.markdownRemark;
  return (
    <Layout>
      <div>
        <h1>{post.frontmatter.title}</h1>
        <div dangerouslySetInnerHTML={{ __html: post.html }} />
        <div>
          <h4>
            Written by: &nbsp; &nbsp;
            {/* {post.frontmatter.author.toUpperCase()} */ <AuthorIs />}
          </h4>
        </div>
      </div>
    </Layout>
  );
};

export const query = graphql`
  query($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        title
        date
        description
        author
        price
      }
    }
  }
`;
